import { logsStore } from "../store/logs";
const FIRST_TIMESTAMP = Date.now();
export const LogGroups = ["EXECUTION", "USER_INPUT", "DEBUG", "UNCAUGHT"];
export const LogTypes = ["UNCHECKED_ERROR", "CHECKED_ERROR", "INFO"];
function error(err, logGroup, checked = false) {
    logsStore.push({
        logType: checked ? "CHECKED_ERROR" : "UNCHECKED_ERROR",
        logGroup,
        timestamp: elapsed(),
        message: checked ? err.message : err.stack
    });
}
function info(message, logGroup) {
    logsStore.push({
        logType: "INFO",
        logGroup,
        timestamp: elapsed(),
        message
    });
}
// milliseconds from when FIRST_TIMESTAMP was set
function elapsed() {
    return Date.now() - FIRST_TIMESTAMP;
}
export default {
    error,
    info
};
