import { writable } from "svelte/store";
const _logsStore = writable([]);
export const logsStore = {
    subscribe: _logsStore.subscribe,
    push(log) {
        _logsStore.update(logs => [...logs, log]);
    }
};
const _loggerStore = writable({
    showLogger: false
});
export const loggerStore = {
    subscribe: _loggerStore.subscribe,
    updateShowLogger(updater) {
        _loggerStore.update(oldVal => (Object.assign(Object.assign({}, oldVal), { showLogger: updater(oldVal.showLogger) })));
    }
};
